import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-nav-bar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.scss'],
})
export class NavBarComponent implements OnInit {
  constructor() {}

  ngOnInit(): void {}

  clickFacebookLogo(): void {
    window.open('https://www.facebook.com/Sateatro', '_blank');
  }
  clickInstagramLogo(): void {
    window.open('https://www.instagram.com/sateatro', '_blank');
  }
}
